@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Rampart+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cedarville+Cursive&display=swap);
:root {
  --primary: #13887e;
  --secondary: #4e4e4e;
  --error: #ff4a4a;
}

/* base styles & title */
body {
  font-family: "Cedarville Cursive";
  color: #4e4e4e;
  color: var(--secondary);
}

.App {
  max-width: 1200px;
  margin: 0 auto;
}

/* upload form styles */


/* progress bar styles */
.progress-bar {
  height: 20px;
  background: #13887e;
  background: var(--primary);
  border-radius: 20px;
  margin-top: 20px;
}

/* image grid styles */


/* modal styles */
.gallery-wall {
    display: flex;
    flex-wrap: wrap;
    margin: auto;
    justify-content: center;
    margin-top: 20px;
}

.img-container {
    max-width: 300px;
    min-height: 300px;
    background: #d3d3d3;
    margin: 15px;
    border-radius: 10px;
    padding: 15px;
}

.img-uploaded-by-me {
    width: 300px;
    border-radius: 10px;
}
.title {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
}

.title h1 {
    font-family: 'Rampart One';
    color: var(--primary);
    font-size: 3rem;
    letter-spacing: 0.1rem;
    font-weight: 900;
}

.title p {
    font-size: 30px;
    font-weight: 500;
}
